.moreBagMiddle{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    margin: 60px 0;
}
.moreBagMiddle2 div img{
    height: 264px;
    width: 264px;
    transform:translateY(-30px) scale(1.3);
}
.moreBagMiddle1 div img{
    height: 264px;
    width: 264px;
    transform: translateY(-30px);
}
.bigMiddle{
    width: 631px;
    height: 360px;
    background: #C6B67E;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    cursor: pointer;
}
.bigMiddle img{
    width: 320px;
    height: 260px;
    transform: scale(1.3);
    margin-bottom: 45px;
}
.bigMiddle div{
    width: 304px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;    
    margin-bottom: 57px;
}
.bigMiddle div h3,.bigMiddle div p{
    color: white;
}


